// app/photo/galerie_page/page.tsx

'use client';

import React, { useState} from 'react';
import Image from 'next/image';

// Utilisation d'import pour les modules fs et path
import fs from 'fs';
import path from 'path';

// Fonction pour récupérer les images côté serveur
async function getImages() {
  const imagesDirectory = path.join(process.cwd(), 'public/img/poster');
  
  // Lire les fichiers du répertoire
  const filenames = fs.readdirSync(imagesDirectory);
  
  // Filtrer les fichiers images
  const imagePaths = filenames
    .filter((filename: string) => !filename.startsWith('.') && filename.match(/\.(jpg|jpeg|png|gif)$/))
    .map((filename: string) => `/img/poster/${filename}`);
  
  return imagePaths;
}

type GalleryPageProps = {
  imagePaths: string[];
};

// Client component
const GalleryPage = ({ imagePaths }: GalleryPageProps) => {
  const [selectedImage, setSelectedImage] = useState<string | null>(null);

  const openImagePopup = (imagePath: string) => {
    setSelectedImage(imagePath);
  };

  const closeImagePopup = () => {
    setSelectedImage(null);
  };

  return (
    <div style={styles.container}>
      {imagePaths.length > 0 ? (
        imagePaths.map((imagePath, index) => (
          <div
            key={index}
            style={{
              ...styles.imageWrapper,
              transform: `rotate(${(index * 10) % 360}deg)`,
              left: `${Math.random() * 80}%`,
              top: `${Math.random() * 80}%`,
            }}
            onClick={() => openImagePopup(imagePath)}
          >
            <Image src={imagePath} alt={`Image ${index}`} width={200} height={200} />
          </div>
        ))
      ) : (
        <p>No images found.</p>
      )}

      {/* Popup for displaying the selected image */}
      {selectedImage && (
        <div style={styles.popupOverlay} onClick={closeImagePopup}>
          <div style={styles.popupContent} onClick={(e) => e.stopPropagation()}>
            <Image src={selectedImage} alt="Selected" width={800} height={800} />
            <button onClick={closeImagePopup} style={styles.closeButton}>X</button>
          </div>
        </div>
      )}
    </div>
  );
};

// Server component: Run getImages and pass to GalleryPage
export default async function Page() {
  const imagePaths = await getImages();

  return <GalleryPage imagePaths={imagePaths} />;
}

// Corrected styles object with React.CSSProperties
const styles: { [key: string]: React.CSSProperties } = {
  container: {
    position: 'relative',
    width: '100%',
    height: '100vh',
    overflow: 'hidden',
  },
  imageWrapper: {
    position: 'absolute',
    transition: 'transform 0.5s ease',
    cursor: 'pointer',
  },
  popupOverlay: {
    position: 'fixed',
    top: 0,
    left: 0,
    width: '100%',
    height: '100%',
    backgroundColor: 'rgba(0, 0, 0, 0.8)',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    zIndex: 1000,
  },
  popupContent: {
    position: 'relative',
    maxWidth: '90%',
    maxHeight: '90%',
    backgroundColor: '#fff',
    padding: '10px',
    borderRadius: '8px',
  },
  closeButton: {
    position: 'absolute',
    top: '10px',
    right: '10px',
    background: 'transparent',
    border: 'none',
    color: '#fff',
    fontSize: '24px',
    cursor: 'pointer',
  },
};
